import { TransactionMode } from '@/types'
import { PoolApiInfo } from '@/types/pool'
import { create, StateCreator } from 'zustand'
import { persist } from 'zustand/middleware'

interface GlobalState {
  pageLoading: boolean
  setPageLoading: (value: boolean) => void
  // 滑点 UI 0.5%,  存 0.005
  slippage: string | number
  setSlippage: (value: string | number) => void
  mevProtect: boolean
  setMevProtect: (value: boolean) => void

  vaultsSlippage: string | number
  setVaultsSlippage: (value: string | number) => void

  zapSlippage: string | number
  setZapSlippage: (value: string | number) => void

  poolApiMap: Record<string, PoolApiInfo[]>
  setPoolApiMap: (key: string, poolAddress: PoolApiInfo[]) => void

  isShowTradeChart: boolean
  setIsShowTradeChart: (isOpen: boolean) => void
  isShowTradeOrders: boolean
  setIsShowTradeOrders: (isOpen: boolean) => void
  settingOpen: boolean
  setSettingOpen: (value: boolean) => void
  transactionMode: TransactionMode
  setTransctionMode: (value: TransactionMode) => void
  maxCapForGas: string
  setMaxCapForGas: (value: string) => void
  customGasPrice: string
  setCustomGasPrice: (value: string) => void
}

const store: StateCreator<GlobalState> = (set, get) => ({
  poolApiMap: {},
  pageLoading: true,
  setPageLoading: (value: boolean) => {
    set(() => ({
      pageLoading: value
    }))
  },
  slippage: 0.005,
  setSlippage: (value: string | number) => {
    set(() => ({
      slippage: value
    }))
  },
  mevProtect: true,
  setMevProtect: (value: boolean) => {
    set(() => ({
      mevProtect: value
    }))
  },

  vaultsSlippage: '0.001',
  setVaultsSlippage: (value: string | number) => {
    set(() => ({
      vaultsSlippage: value
    }))
  },

  zapSlippage: '0.005',
  setZapSlippage: (value: string | number) => {
    set(() => ({
      zapSlippage: value
    }))
  },

  setPoolApiMap: (key: string, poolApiList: PoolApiInfo[]) => {
    const poolApiMap = get().poolApiMap
    poolApiMap[key] = poolApiList
    set(() => ({
      poolApiMap: { ...poolApiMap }
    }))
  },
  isShowTradeChart: false,
  setIsShowTradeChart: (isOpen: boolean) => {
    set(() => ({
      isShowTradeChart: isOpen
    }))
  },
  isShowTradeOrders: false,
  setIsShowTradeOrders: (isOpen: boolean) => {
    set(() => ({
      isShowTradeOrders: isOpen
    }))
  },
  settingOpen: false,
  setSettingOpen: (value: boolean) => {
    set(() => ({
      settingOpen: value
    }))
  },
  transactionMode: 'Default',
  setTransctionMode: (value: TransactionMode) => {
    set(() => ({
      transactionMode: value
    }))
  },
  maxCapForGas: '0.5',
  setMaxCapForGas: (value: string) => {
    set(() => ({
      maxCapForGas: value
    }))
  },
  customGasPrice: '1500',
  setCustomGasPrice: (value: string) => {
    set(() => ({
      customGasPrice: value
    }))
  }
})

const useGlobalStore = create(
  persist(store, {
    name: 'useGlobalStore',
    partialize: state => {
      const { slippage, mevProtect, vaultsSlippage, isShowTradeChart, transactionMode, maxCapForGas, customGasPrice, zapSlippage } = state
      return { slippage, mevProtect, vaultsSlippage, isShowTradeChart, transactionMode, maxCapForGas, customGasPrice, zapSlippage }
    },
    version: 3
  })
)
export default useGlobalStore
